*{
    margin: 0;
    box-sizing: border-box;
    font-family: 'Jost', sans-serif;
}
#root{
    min-height: 100vh;
    display: grid;
    place-items: center;
    background: whitesmoke;
}
.main{
    min-height: 60vh;
    min-width: 30%;
    background: #00b9e7b5;
    /* border: 10px solid white; */
    border-radius: 10px;
}
.create-ani{
    min-height: 40vh;
    width: 100%;
    border-radius: 25% 0% 0% 25%;
    border-radius: 57% 43% 45% 0% / 36% 51% 0% 64% ;
    background: whitesmoke;
    position: relative;
}
.wave{
    height: 300px;
    width: 300px;
    background: rgba(245, 245, 245,.3);
    border-radius: 65% 35% 68% 32% / 36% 61% 39% 64%  ;
    position: absolute;
    animation: drift 1s infinite ;
     /* duration timing-function delay iteration-count direction fill-mode; */
}
.one{
    top: -40px;
    left: -20px;
    z-index: 0;
    animation: drift 500ms infinite;
}
.two{
    top: -60px;
    	left: 90px;
        z-index: 0;
        animation: drift 200ms infinite;
}
.three{
    top: -50px;
    left: 170px;
    z-index: 0;
    animation: drift 300ms infinite;
}
.text-data{
    
    position: absolute;
    left: 0;
    right: 0;
    
    z-index: 1;
}
@keyframes drift {
    from{
        transform: rotate(0deg);
    }
    to{
        transform: rotate(360deg);
    }
}
@media only screen and (max-width: 600px){
    .main{
        width: 100%;
        height: 100%;
    }
    .create-ani{
        position: fixed;
        bottom: 0;
    }
    .one{
        left: -30px;
        top: -20px;
    }
    .two{
        
        left: 70px;
        top: -45px;
    }
    .three{
        
        left: 150px;
        top: -35px
    }
}